<template>
  
</template>

<script>

import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader'
import {Text} from 'troika-three-text'

export default {
  mounted() {
    

    const scene = new THREE.Scene();

    const camera = new THREE.PerspectiveCamera(
      75,
      window.innerWidth / window.innerHeight,
      0.1,
      1000
    );

    camera.position.set(170, 0, 0);

    const container = document.querySelector('#app')

    const renderer = new THREE.WebGLRenderer();
    renderer.shadowMap.enabled = true;

    renderer.setSize(container.offsetWidth, container.offsetHeight)

    const controls = new OrbitControls(camera, renderer.domElement);

    const light = new THREE.DirectionalLight( 0xffffff, 1, 100 );
    light.position.set(0.2, 0.5, -1);
    light.castShadow = true;
    light.shadow.mapSize.width = 2048;
    light.shadow.mapSize.height = 2048;
    light.shadow.radius = 8;
    scene.add( light );

    const hemiLight = new THREE.HemisphereLight( 0xffffff, 0xffffff, 0.61 );
    hemiLight.position.set( 0, 50, 0 );
    scene.add(hemiLight); 

    container.appendChild(renderer.domElement)

    const loader = new OBJLoader();

    loader.load("Heart.obj", (data) => {
      data.children[0].geometry.translate(0, -5, 0);

      data.children[0].material = new THREE.MeshStandardMaterial({ color: 0xFF071E, side: THREE.FrontSide, opacity: 0.5, transparent: true });
      data.children[0].material.receiveShadow = true;
      data.children[0].material.castShadow = true;
      data.rotateY(Math.PI / 2)
      scene.add(data);
      requestAnimationFrame(animate);
    });

    loader.load("croissant.obj", (data) => {
      data.children[0].geometry.translate(0, -5, 0);

      data.children[0].material = new THREE.MeshStandardMaterial({ color: 0xDE843E });
      data.children[0].material.receiveShadow = true;
      data.children[0].material.castShadow = true;
      data.rotateY(Math.PI / 2)
      data.scale.set(30, 30, 30)
      data.position.y = 150
      data.position.z = 40
      scene.add(data);
      requestAnimationFrame(animate);
    });

      const me = new Text()
      scene.add(me)
      me.rotateY(Math.PI / 2)
      me.position.set(0, 65, -30)
      me.text = 'my heart'
      me.fontSize = 5
      me.color = 0xFFFFFF
      me.sync()

      const you = new Text()
      scene.add(you)
      you.rotateY(Math.PI / 2)
      you.position.set(0, 40, 55)
      you.text = 'you'
      you.fontSize = 10
      you.color = 0xFFFFFF
      you.sync()


    window.addEventListener("resize", () => {
      camera.aspect = window.innerWidth / window.innerHeight;
      camera.updateProjectionMatrix();
      renderer.setSize(container.offsetWidth, container.offsetHeight)
    });

    function animate(time) {
      renderer.render(scene, camera);
      requestAnimationFrame(animate);
    }
    

  }
}
</script>

<style lang="scss">
  *{
    margin: 0;
    padding: 0;
  }

  #app{
    width: 100vw;
    height: 100vh;
    position: relative;
  }
</style>
